* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    list-style-type: none;
    text-decoration: none;
}

:root {
    --lato: 'Lato', sans-serif;
    --lemon: 'Lemon', cursive;
    --open: 'Open Sans', sans-serif;
}

header {
    background: url(./img/./header.png) no-repeat center center / cover;
    padding: 30px 0px 186px 0px;
}

header .navbar {
    padding: 0;
}

header .logo .navbar-brand {
    text-transform: capitalize;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 30px;
    font-weight: 900;
    color: white;
}

header .navbar .nav-link {
    padding: 0;
    font-weight: 700;
    font-family: var(--lato);
    font-size: 15px;
    text-transform: uppercase;
    margin-left: 25px;
    color: white;
}

.navbar-expand-lg .navbar-nav .nav-link {
    padding: 0;
}

header .header-text {
    position: relative;
}

header h1, 
header p {
    margin-top: 175px;
    font-family: sans-serif;
    font-size: 159px;
    text-transform: uppercase;
    font-weight: 900;
    color: white;
    text-align: center;
    line-height: 120px;
    letter-spacing: -10px;
    /* background-clip: text!important; */
}

header p {
    font-size: 100px;
    padding: 0;
    margin: 0;
    margin-top: -25px;
}

header .btn {
    display: inline-block;
    margin-top: 45px;
    background-color: #fff;
    color: #000;
    text-transform: uppercase;
    padding: 10px 28px;
    border-radius: 35px;
    font-family: var(--lato);
    font-weight: 700;
}

header .img {
    position: absolute;
    right: 0px;
    top: 180px;
    z-index: 1000;
}

header .img img {
    max-width: 650px;
}

/* Section Starting */

/* About-first Starting  */

section .about-first {
    position: relative;
    padding-top: 100px;
    padding-bottom: 115px;
}

section .about-first::before {
    content: "";
    position: absolute;
    top: -4%;
    right: 0%;
    width: 1080px;
    height: 580px;
    background-color: black;
    clip-path: polygon(15% 0, 100% 0, 100% 23%, 73% 100%);
}

section .about-first .mac {
    position: absolute;
    top: 20px;
    right: 111px;
    max-width: 345px
}

section .about-first h3, section .content h3, section .album-page h3, section .carousel-page h3, .last-about h3, footer h3 {
    font-family: var(--open);
    font-weight: 700;
    font-size: 52px;
    color: #494949;
}

section .about-first, section .content p, section .album-page p {
    margin-top: 20px;
    line-height: 30px;
    font-family: var(--open);
    font-weight: 500;
    font-size: 17px;
    color: #6a6a6a;
}

section .about-first .btn, .album-page .btn {
    background-color: #bc2e3a;
    color: #fff;
    text-transform: uppercase;
    padding: 10px 28px;
    border-radius: 35px;
    font-family: var(--lato);
    font-weight: 700;
    margin-top: 28px;
}

/* Section - content - Starting */

section .content {
    background: #f5f5f5;
    padding-top: 95px;
    padding-bottom: 116px;
    position: relative;
}

section .content::before {
    content: "";
    position: absolute;
    top: 45%;
    left: 0%;
    width: 120px;
    height: 191px;
    clip-path: polygon(72% 40%, 0 0, 0 80%);
    background-color: #bc2e3a;
}

section .content h3 {
    text-align: center;
    margin-bottom: 88px;
}

section .content .img {
    background-color: #bc2e3a;
    padding: 15px;
    margin-right: 11px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

section .content .item {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

section .content h5 {
    font-family: var(--open);
    font-weight: 800;
    font-size: 30px;
    color: #6f6f6e;
    text-transform: capitalize;
}

/* Section album-page Starting */

section .album-page h3 {
    margin-bottom: 65px;
}

section .album-page {
    position: relative;
    padding-top: 120px;
    padding-bottom: 150px;
}

section .album-page img {
    width: 100%;
    height: 320px;
}

section .album-page .img-box {
    position: relative;
    clip-path: polygon(26% 0%, 95% 0%, 75% 79%, 6% 79%);
    margin-left: -20px;
    background-color: rgb(66, 66, 64);
    z-index: 123;
}

section .album-page .img-box p, section .album-page .img-box h5 {
    position: absolute;
    top: 0%;
    left: 70%;
    font-size: 40px;
    font-weight: 700;
    font-family: var(--lato);
    color: white;
    font-style: italic;
    text-transform: uppercase;
}

section .album-page .img-box h5 {
    top: 58%;
    left: 11%;
}

section .album-page::before {
    content: "";
    position: absolute;
    top: -40%;
    right: 0%;
    width: 50%;
    height: 1200px;
    background-color: #bc2e3a;
    clip-path: polygon(100% 100%, 30% 50%, 100% 0);
    z-index: 5;
}

/* Carousel-page-starting */

section .carousel-page {
    padding-top: 110px;
    height: 800px;
    background-color: #f5f5f5;
    z-index: 1;
    position: relative;
}

section .carousel-page::before {
    content: "";
    position: absolute;
    top: -235px;
    left: 0;
    background-color: #171717;
    width: 220px;
    height: 470px;
    clip-path: polygon(0 0, 90% 50%, 0 100%);
}

section .carousel-page h3 {
    text-align: center;
    margin-bottom: 125px;
}

section .carousel-page .cards {
    transform: rotate(-45deg);
    border: 20px solid white;
    height: 320px;
    overflow: hidden;
    position: relative;
    box-shadow: 1px 1px 40px black;
}

section .carousel-page img {
    transform: rotate(45deg) scale(1.4);
    display: block;
}

section .carousel-page .body-text {
    position: absolute;
    top: 420px;
    left: -55px;
    transform: rotate(45deg);
    font-family: var(--open);
    font-weight: 700;
    font-size: 22px;
    text-transform: capitalize;
    padding: 10px 60px;
    color: white;
    background-color: #252525;
    z-index: 1500!important;
}

section .carousel-page .cards:nth-child(2) {
    transform: rotate(-45deg) scale(1.1);
}

section .carousel-page .button {
    display: flex;
    position: relative;
    top: -50%;
    justify-content: space-around;
    z-index: 111111;
}

section .carousel-page .button .left, .right {
    font-size: 200px;
    border: none;
    background-color: transparent;
}

section .carousel-page .button .left {
    position: absolute;
    left: 0%;
}

section .carousel-page .button .right {
    position: absolute;
    right: 0%;
    z-index: 25;
}

/* Last-about Starting */

section .last-about {
    position: relative;
    padding-top: 100px;
    padding-bottom: 180px;
}

section .last-about h3 {
    text-align: center;
    margin-bottom: 120px;
}

section .last-about .img {
    z-index: 100!important;
    border: 30px solid white;
    transform: rotate(-45deg);
    overflow: hidden;
    box-shadow: 1px 1px 40px black;
}

section .last-about .img img {
    transform: rotate(45deg) scale(1.5);
}

section .last-about h3::after {
    content: "";
    position: absolute;
    left: 0;
    top: -5%;
    width: 470px;
    height: 1000px;
    background-color: #bc2e3a;
    clip-path: polygon(100% 50%, 0 0, 0 100%);
    z-index: 1;
}

section .last-about h3::before {
    content: "";
    position: absolute;
    top: 0%;
    width: 310px;
    height: 310px;
    background-color: #171717;
    right: 0%;
    clip-path: polygon(100% 0, 0 0, 100% 100%);
}

section .last-about p {
    font-style: italic;
    font-size: 20px;
    color: #6a6a6a;
    font-weight: 500!important;
}

section .last-about p span {
    font-weight: 900;
    font-size: 200px;
    position: absolute;
    top: -35%;
    left: -15%;
    font-family: Georgia, 'Times New Roman', Times, serif;
}

section .last-about .bold {
    font-weight: 700 !important;
    font-size: 18px;
}

section .last-about .img-page {
    margin-top: 80px;
}

section .last-about .img-page .box {
    width: 95px;
    height: 95px;
    margin-right: 25px;
}

section .last-about .img-page img {
    width: 100%;
    height: 100%;
    clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
}

/* Footer Starting */

footer {
    position: relative;
    background: url(./img/footer.png) no-repeat center center / cover;
    padding-top: 90px;
    padding-bottom: 20px;
}

footer h3 {
    text-align: center;
    margin-bottom: 90px;
    color: #9f9f9f;
}

footer .box {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    color: white;
}

footer .box .body-text {
    margin-left: 30px;
}

footer .box .body-text a {
    color: white;
}

footer .box .body-text a:hover {
    color: coral;
}

footer .box .body-text span {
    font-family: var(--open);
    font-size: 18px;
}

footer .box .body-text p {
    font-family: var(--open);
    font-size: 22px;
    font-weight: 700;
    margin-bottom: 8px;
}

footer .icon-page {
    border-bottom: 2px solid #fff;
    padding-bottom: 36px;
    margin-top: 100px;
}

footer .icon-page a {
    color: white;
    margin-left: 30px;
    font-size: 30px;
}

footer .copy {
    color: white;
    font-size: 18px;
    text-align: center;
    font-family: serif;
    margin-top: 35px;
}

footer .copy::after {
    content: "";
    position: absolute;
    bottom: 0;
    right: 0;
    width: 310px;
    height: 170px;
    background-color: #bc2e3a;
    clip-path: polygon(51% 0%, 100% 78%, 100% 100%, 0 100%);
}

/* Media Query Startng */

/* XL Min Width 1200pX Starting */

@media (min-width:1200px) {
    section .about-first::before {
        top: -4%;
        width: 980px;
    }
    section .album-page h3 {
        text-align: center;
    }
   
}

/* Large max-width:1199px */

@media (max-width:1199px) {
    header {
        padding: 30px 0px 100px 0px;
    }
    header .img img {
        max-width: 525px;
    }
    header h1 {
        font-size: 135px;
    }
    header p {
        font-size: 85px;
    }
    /* Section Starting */
    /* About First Starting */
    section .about-first::before {
        content: "";
        top: -5%;
        clip-path: polygon(38% 0, 100% 0, 100% 36%, 75% 100%);
    }
    section .about-first .mac {
        top: 18px;
        right: 130px;
        max-width: 445px;
    }
    section .about-first::before {
        top: -3%;
        right: 0%;
        width: 310px;
        height: 310px;
        clip-path: polygon(0 0, 100% 100%, 100% 0);
    }
    section .about-first {
        padding-bottom: 35px;
    }
    /* Album Page Starting */
    section .album-page::before {
        content: "";
        position: absolute;
        top: 0%;
        right: 0%;
        width: 310px;
        height: 310px;
        background-color: #bc2e3a;
        clip-path: polygon(0 0, 100% 100%, 100% 0);
    }
    section .album-page h3 {
        text-align: center;
    }
    section .album-page .btn {
        margin-bottom: 30px;
    }
    section .album-page::after {
        content: "";
        position: absolute;
        bottom: 0%;
        left: 0%;
        background-color: #171717;
        width: 280px;
        height: 280px;
        clip-path: polygon(0 0, 100% 100%, 0 100%);
    }
    /* Carosuel Page Starting */
    section .carousel-page::before {
        display: none;
    }
    section .carousel-page .cards {
        height: 275px;
    }
    /* section .carousel-page .body-text {
        top: 160px;
    } */
    section .carousel-page .button {
        top: -47%;
    }
    /* Last About Starting */
    section .last-about h3::after {
        display: none;
    }
    section .last-about::after {
        content: "";
        position: absolute;
        left: 0%;
        bottom: 0%;
        height: 250px;
        width: 250px;
        background-color: #bc2e3a;
        clip-path: polygon(0 0, 100% 100%, 0 100%);
    }
    section .last-about h3::before {
        width: 250px;
        height: 250px;
    }
    section .last-about .img {
        height: 310px;
    }
    section .last-about p span {
        top: -33%;
        left: -15%;
    }
    /* Footer Starting */
    footer .copy::after {
        top: 0%;
        right: 0%;
        height: 310px;
        clip-path: polygon(0 0, 100% 100%, 100% 0);
    }
}

/* max-width:  991px Starting */

@media (max-width:991px) {
    header .img img {
        display: none;
    }
    header .navbar .navbar-toggler-icon .fa {
        color: white;
        font-size: 35px;
    }
    header .navbar-expand-lg .navbar-nav .nav-link {
        margin-bottom: 10px;
    }
    header .navbar-nav {
        margin-top: 20px;
    }
    header .header-text {
        margin: auto;
    }
    header h1 {
        margin-top: 100px;
        font-size: 100px;
    }
    header p {
        font-size: 75px;
        letter-spacing: -2px;
    }
    /* Section Starting */
    /* About First Starting */
    section .about-first .mac {
        max-width: 430px;
    }
    section .about-first::before {
        top: -3%;
    }
    /* Content Starting */
    section .content {
        padding-bottom: 50px;
    }
    section .content::before {
        display: none;
    }
    section .content::after {
        content: "";
        position: absolute;
        top: 0%;
        left: 0%;
        width: 250px;
        height: 250px;
        background-color: #bc2e3a;
        clip-path: polygon(100% 0, 0 100%, 0 0);
    }
    /* Album-Page Starting */
    section .album-page::before {
        clip-path: polygon(25% 0, 100% 75%, 100% 0);
    }
    section .album-page::after {
        width: 235px;
        height: 220px;
    }
    section .album-page .btn {
        margin-left: 12px;
    }
    /* Carousel Starting */
    section .carousel-page {
        display: none;
    }
    /* Last About Starting */
    section .last-about {
        padding-bottom: 80px;
    }
    section .last-about .img {
        z-index: 0;
        border: none;
        transform: none;
        overflow: unset;
        box-shadow: none;
        margin: auto;
        margin-bottom: 40px;
    }
    section .last-about .img img {
        transform: none;
    }
    section .last-about h3 {
        margin-bottom: 40px;
    }
    section .last-about p span {
        display: none;
    }
    section .last-about .img-page {
        margin-top: 20px;
    }
    /* Footer Starting */
    footer .copy::after {
        display: none;
    }
    footer h3 {
        margin-bottom: 40px;
    }
    footer .icon-page {
        margin-top: 40px;
    }
}

/* Media 767px Starting */

@media (max-width:767px) {
    /* Header Starting  */
    header {
        padding: 25px 0px 45px 0px;
    }
    header h1 {
        margin-top: 45px;
        font-size: 85px;
        letter-spacing: 0;
        line-height: 100px;
    }
    header p {
        font-size: 55px;
    }
    header .btn {
        margin-top: 0;
    }
    /* Section Starting */
    /* About First Starting */
    section .about-first::before {
        width: 220px;
        height: 220px;
    }
    section .about-first .mac {
        max-width: 296px;
        top: 23px;
        right: 110px;
    }
    /* Content Starting */
    section .content::after {
        width: 200px;
        height: 200px;
    }
    /* Album Page Starting */
    section .album-page::before {
        width: 200px;
        height: 200px;
    }
    section .album-page::after {
        display: none;
    }
    section .album-page {
        padding-bottom: 20px;
    }
    /* Last About Starting */
    section .last-about h3::before, section .last-about::after {
        width: 160px;
        height: 160px;
    }
    section .last-about .img {
        margin-bottom: -30px;
    }
}

/* Max-Width 575pX Starting */

@media (max-width:575px) {
    header h1 {
        font-size: 50px;
        line-height: 58px;
    }
    header p {
        font-size: 30px;
        margin-top: -50px;
    }
    /* About First Starting */
    section .about-first p {
        font-size: 14px;
    }
    section .about-first::before {
        width: 150px;
        height: 150px;
        top: -5%;
    }
    /* Content Starting */
    section .content::after {
        width: 150px;
        height: 150px;
    }
    section .about-first h3, section .content h3, section .album-page h3, section .carousel-page h3, .last-about h3, footer h3 {
        font-size: 45px;
    }
    section .last-about h3::before, section .last-about::after {
        width: 130px;
        height: 130px;
    }
}

/* Media 319px Starting  */

@media (max-width:320px) {
    header .navbar .navbar-toggler-icon .fa {
        font-size: 30px;
    }
    header .logo .navbar-brand {
        font-size: 25px;
    }
    header h1 {
        margin-top: 40px;
        font-size: 34px;
        line-height: 45px;
    }
    header p {
        font-size: 20px;
        line-height: 75px;
        margin-top: -25px;
    }
    header .btn {
        padding: 5px 15px;
        font-size: 14px;
    }
    /* About First Starting */
    section .about-first::before {
        display: none;
    }
    section .about-first h3, section .content h3, section .album-page h3, section .carousel-page h3, .last-about h3, footer h3 {
        font-size: 30px;
    }
    section .about-first {
        padding-top: 15px;
    }
    /* Content Starting */
    section .content {
        padding-top: 45px;
        padding-bottom: 0;
    }
    section .content::after {
        display: none;
    }
    section .content h3 {
        margin-bottom: 50px;
    }
    section .content h5 {
        font-size: 16px;
    }
    section .about-first, section .content p, section .album-page p {
        font-size: 14px;
        line-height: 25px;
    }
    /* Album Page Starting */
    section .album-page::before {
        display: none;
    }
    section .album-page {
        padding-top: 30px;
    }
    section .album-page h3 {
        margin-bottom: 30px;
    }
    section .album-page .btn {
        margin-left: 0;
    }
    section .album-page .img-box {
        display: none;
    }
    /* Last About Starting  */
    section .last-about h3::before, section .last-about::after {
        display: none;
    }
    section .last-about {
        border-top: 1px solid black;
        padding-top: 20px;
    }
    section .last-about .img {
        height: auto;
        margin-bottom: 10px;
    }
    section .last-about p {
        font-size: 16px;
    }
    section .last-about .img-page .box {
        width: 50px;
        height: 50px;
    }
    section .last-about {
        padding-bottom: 20px;
    }
    /* Footer Starting */
    footer {
        padding-top: 50px;
    }
    footer .box .body-text p {
        font-size: 18px;
    }
    footer .box .body-text span {
        font-size: 16px;
    }
    footer .icon-page a {
        margin-left: 3px;
        font-size: 25px;
    }
    footer .copy {
        font-size: 12px;
    }
}